function copy (source) {
    switch(typeof source) {
        case 'number':
        case 'string':
        case 'function':
        default:
            return source;
        case 'object':
            if (source === null) {
                return null;
            }
            else if (Array.isArray(source)) {
                return source.map (item => copy (item));
            }
            else if (source instanceof Date) {
                return source;
            }
            else {                
                return Object.keys(source).reduce((a, k) => {                    
                    a[k] = copy(source[k]);
                    return a;
                }, {});
            }
    }
}

function merge (target, source) {
    if (target === source) {
	    return target;
    }
    else {
        return Object.keys(source).reduce((a, k) => {
            let value = source[k];
            if(typeof a[k] === 'object' && (k in a)){
                a[k] = merge(a[k], value);
            }
            else {
               a[k] = copy(value);
            }
            return a;
        }, copy(target));
    }    
}

class Translation {
    constructor(){
        this._langs = {};        
        let current = localStorage.getItem('lang');
        if (!current) {
            current = 'ru';            
            localStorage.setItem('lang', current);
        }     
        this._current = current;        
    }
    getLanguage () {
	return this._current;
    }
    addText (lang, obj) {        
        this._langs[lang] = this._langs[lang] || {};        
        this._langs[lang] = merge(this._langs[lang], obj);
    }    
    getText (path) {        
        return this._translate (this._langs[this._current], path);
    }
    _translate (root, path) {
        const i = path.indexOf('.');
        if (i >= 0) {
            return this._translate(root[path.substring(0, i)], path.substring(i + 1));
        }
        else {
            return root[path];
        }
    }
}

const T = new Translation();

export default T;